export function generateAuthError(code: string) {
  switch (code) {
    case 'INVALID_PASSWORD':
      return ' /';
    case 'EMAIL_EXISTS':
      return 'email ';
    case 'LOGIN_NOT_FOUND':
      return '  ';
    case 'INVALID_DATA':
      return '   ';
		case 'INVALID_EMAIL':
			return '   email';

    default:
      return code;
  }
}