import { toast } from 'react-toastify';

export function validate(field:string, data:string, registrationPassword?:string ) {
	switch(field) {
		case "registrationName": {
			if (String(data).trim().match(/^[a-zA-Z--- ]+[a-zA-Z--- ]+[a-zA-Z--]{1,40}$/)) {
				return false
			} else {
				toast.error("  ,       ,  3 ")
				return true
			}
		}

		case "registrationEmail": {
			if (String(data).trim().toLowerCase().match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)) {
				return false
			} else {
				toast.error("email     user@onlinestore.com")
				return true
			}
		}

		case "registrationPhone": {
			if (String(data).toLowerCase().match(/^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/)) {
				return false
			} else {
				toast.error("     ,   +71234567890  81234567890")
				return true
			}
		}

		case "registrationPassword": {
			if (String(data).match(/^[a-zA-Z0-9]{3,}/)) {
				return false
			} else {
				toast.error("    ,     ")
				return true
			}
		}

		case "registrationConfirmPassword": {
			if (String(data) === String(registrationPassword)) {
				return false
			} else {
				toast.error("  ")
				return true
			}
		}
	}
}
